import * as React from 'react';
import Link from '@mui/material/Link';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Title from './Title';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import PaymentIcon from '@mui/icons-material/Payment';

// Generate Order Data
function createData(id, date, name, shipTo, paymentMethod, amount) {
  return { id, date, name, shipTo, paymentMethod, amount };
}

const rows = [
  createData(
    0,
    '16-05-2022',
    'Tramite 1',
    'Solicitado',
    'Pago pendiente',
    3,
  ),
  createData(
    1,
    '15-05-2022',
    'Tramite 2',
    'Procesando',
    'Pago en linea',
    8,
  ),
  createData(2, '16-05-2022', 'Tramite 5', 'Procesando', 'N/S', 0),
  createData(
    3,
    '01-05-2022',
    'Tramite 3',
    'Finalizado',
    'Pago en linea',
    6,
  ),
  createData(
    4,
    '30-06-2022',
    'Tramite 8',
    'Finalizado',
    'N/S',
    0,
  ),
];

function preventDefault(event) {
  event.preventDefault();
}

export default function MisTramites() {
  return (
    <React.Fragment>
      <Title>Mis tramites</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Codigo</TableCell>
            <TableCell>Fecha de solicitud</TableCell>
            <TableCell>Tramite</TableCell>
            <TableCell>Estado</TableCell>
            <TableCell>Metodo de pago</TableCell>
            <TableCell align="right">Costo</TableCell>
            <TableCell align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell>TRA-{row.id}</TableCell>
              <TableCell>{row.date}</TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell>{row.shipTo}</TableCell>
              <TableCell>{row.paymentMethod}</TableCell>
              <TableCell align="right">{`$${row.amount}`}</TableCell>
              <TableCell align="right">
                {
                    row.paymentMethod !== 'Pago pendiente' 
                    ? <RemoveRedEyeIcon/>
                    : <PaymentIcon/>   
                }
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </React.Fragment>
  );
}